import React, { useState, useEffect } from "react";
// import styled from "styled-components";

// 
const MotionRec = () => {
  // let msec = [];
  const [outData, setOutData] = useState([]);
  // useState
  const [data, setData] = useState([]); // 
  // useState
  const [button, setButton] = useState(false); // false
  // useEffect
  const [testOutData, setTestOutData] = useState([]);
  const [testOutData2, setTestOutData2] = useState([]);
  useEffect(() => {
    const handleDeviceMotion = (event) => {     // 
      const { x, y, z } = event.acceleration;   // 
      const msec = performance.now();
      let msec1 = performance.now();
      let testData = []
      let calc = 0
      let x1
      for (let i = 0; i < 2; i++) {
        for (let i = 0; i < 10000000; i++) {calc = i * i}
        msec1 = performance.now();
        x1 = event.acceleration.x
        testData.push([msec1, x1]);
      }

      setTestOutData(testData)

      setData((prevData) => {                   // 
        if (prevData.length >= 10) { prevData.shift() } // 1024
        // if (prevData.length >= 10) { 
          // setButton((prevButton) => !prevButton);
          // return;
        //  } // 1024
        return [...prevData, {msec, x, y, z}];      // 
        // return [...prevData, msec];      // 
      });
    };
    
    // 
    if (button) {
      // true
      //. 
      DeviceMotionEvent.requestPermission().then( function( response ){
        if( response === 'granted' ){
          // window.addEventListener( "devicemotion", handleDeviceMotion );
          window.addEventListener("devicemotion", handleDeviceMotion, { frequency: 200 });
        }
      }).catch( function( e ){console.log( e )})
    } else {
      // false
      window.removeEventListener("devicemotion", handleDeviceMotion);
    }
    // 
    return () => {
      // 
      window.removeEventListener("devicemotion", handleDeviceMotion);
    };
  }, [button]); // 
  
  // 
  const handleClick = () => {
    // setButton
    setButton((prevButton) => !prevButton);
  };

  const refreshData = () => {
    console.log(data)
    setOutData(JSON.stringify(data))
    setTestOutData2(testOutData)
  }

  useEffect(() => {
    setTestOutData2(testOutData);
  }, [testOutData]);

  // JSX
  return (
    <div>
      {/* <p>: {JSON.stringify(data)}</p> */}
      <button onClick={refreshData}>refresh</button>
      <p>out1:{outData}</p>
      <p>out2:{testOutData}</p>
      <p>out3:{testOutData2}</p>
      <button onClick={handleClick}>{button ? "" : ""}</button>
    </div>
  );
};

// 
export default MotionRec;
