import React from 'react'

const Output = (props) => {
  const orgRound = (value, base) => {
    return Math.round(value * base) / base
  }
  return (
    <div>
      <h4></h4>
      <p>(max):{ orgRound(props.stressCalc, 100) }</p>
      <p>():{ orgRound(props.beamCalc, 100) }</p>
      <p>(p):{ orgRound(props.beamCalcP, 100) }</p>
      <p>(m):{ orgRound(props.beamCalcM, 100) }</p>
      <p>(I): { orgRound(props.secondMoment, 10) } mm<sup>4</sup></p>
      <p>(Z): { orgRound(props.sectionCoefficient, 10) } mm<sup>3</sup></p>
      <p>(A): { props.sectionArea } mm<sup>2</sup></p>
      <p>(w):{props.mass}N</p>
    </div>
  )
}

export default Output




