import React, { useState } from 'react'
import LoadSet01css from "./LoadSet01.module.css"

const LoadSet01 = (props) => {
  const [loadValue, setLoadValue] = useState("");
  props.setLoadValue(loadValue)
  return (
    <div>
      <div className={LoadSet01css.loadSet}>
      <input className={LoadSet01css.loadSetInput} id="11" value="" type="radio" name="LoadSet" onChange={() => setLoadValue("")} defaultChecked />
      <label for="11"></label>
      <input className={LoadSet01css.loadSetInput} id="12" value="" type="radio" name="LoadSet" onChange={() => setLoadValue("")} />
      <label for="12"></label>
      </div>

    </div>
  )
}

export default LoadSet01