import React from 'react'

function copyTextToClipboard(text) {
  navigator.clipboard.writeText(text)
  .then(function() {
    console.log('Async: Copying to clipboard was successful!');
  }, function(err) {
    console.error('Async: Could not copy text: ', err);
  });
}

const copy = (props) => {
  const clipBoardData =  [
    `\t\t
    \t()\t${props.calcStress}\tMPa
    ()\t()\t${props.calcBeamCalc}\tmm
    ()\t(p)\t${props.calcBeamCalcP}\tmm
    ()\t(m)\t${props.calcBeamCalcM}\tmm
    \t(I)\t${props.calcSecondMoment}\tmm4
    \t(Z)\t${props.calcSectionCoefficient}\tmm3
    \t(A)\t${props.calcSectionArea}\tmm2

    \t\t${props.selectItem}
    \t(E)\t${props.young}\tGPa
    \t()\t${props.density}\tg/cm
    \t(L)\t${props.length1}\tmm
    \t(p)\t${props.load1}\tN
    \t(m)\t${props.mass}\tN
    1\t(${props.dimSymbol1})\t${props.dim1}\tN
    2\t(${props.dimSymbol2})\t${props.dim2}\tN
    3\t()\t\tN
    4\t()\t\tN
    `]
    
  return (
    <div>
        <p>Excel/</p>
        <button onClick={() => copyTextToClipboard( clipBoardData )}>
        
        </button>
    </div>
  )
}

export default copy
