import React, { useState } from 'react';
import Suportcss from "./Suport.module.css"

const Suport = (props) => {
  const [suportValue, setSuportValue] = useState("");
  props.setSuportValue(suportValue)
  return (
    <div>
      <h4>/</h4>
      <div className={Suportcss.suport}>
        <input className={Suportcss.suportInput} id="01" value="" type="radio" name="suport" onChange={() => setSuportValue("")} defaultChecked />
        <label for="01"></label>
        <input className={Suportcss.suportInput} id="02" value="" type="radio" name="suport" onChange={() => setSuportValue("")} />
        <label for="02"></label>
        <input className={Suportcss.suportInput} id="03" value="" type="radio" name="suport" onChange={() => setSuportValue("")} />
        <label for="03"></label>
      </div>
    </div>
  )
}

export default Suport